<template>
    <v-layout fluid>
        <v-card width="100%">
            <v-card-title class="justify-center">Códigos UNSPSC</v-card-title>
            <v-card-text>
                <div style="padding-bottom:10px">
                    <v-btn v-if="codigo != '0' && codigo != ''" fab dark small color="primary" @click="dialog = true, itemactual=''">
                        <v-icon dark>add</v-icon>
                    </v-btn>
                    <v-text-field
                        v-model="search"
                        append-icon="search"
                        label="Buscar"
                        single-line
                        hide-details
                    ></v-text-field>
                    <v-data-table :headers="headers" :items="dataUnspsc" :items-per-page="5" :disable-sort="true" :search="search">
                        <template v-slot:item.action="{ item }">
                            <v-icon
                                small
                                class="mr-5"
                                color="primary"
                                @click="dialog_del=true, itemactual=item"
                            >
                                delete
                            </v-icon>
                        </template>
                    </v-data-table>
                </div>
            </v-card-text>
        </v-card>

        <v-dialog v-model="dialog" max-width="900px">
            <v-card>
                <v-card-title>
                    <span class="headline">Agregar Códigos Unspsc</span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap column>
                            <v-form v-model="valid" ref="form">
                                <v-row>
                                    <v-col cols="5">
                                        <v-select
                                            :items="arrfiltro"
                                            item-value="value"
                                            item-text="text"
                                            v-model="filtro"
                                            :return-object="false"
                                            label="Tipo de Filtro"
                                        >
                                        </v-select>
                                    </v-col>
                                    <v-col cols="5">
                                        <v-text-field
                                            v-model="valfiltro"
                                            label="Dato"
                                        ></v-text-field>
                                    </v-col>
                                    <v-col cols="2">
                                        <v-btn class="mx-2" fab dark small color="primary" @click="traerDataXFiltro">
                                            <v-icon dark>search</v-icon>
                                        </v-btn>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col sm12>
                                        <v-text-field
                                            v-model="searchfiltro"
                                            append-icon="search"
                                            label="Buscar"
                                            single-line
                                            hide-details
                                        ></v-text-field><br>
                                        <v-data-table
                                            :headers="headersbusqueda"
                                            :items="dataBusq"
                                            :items-per-page="5"
                                            :disable-sort="true"
                                            :search="searchfiltro"
                                        >
                                            <template v-slot:item.action="{ item }">
                                                <v-icon
                                                    small
                                                    class="mr-5"
                                                    color="primary"
                                                    @click="seleccionarItem(item)"
                                                >
                                                    send
                                                </v-icon>
                                            </template>
                                        </v-data-table>
                                        <br>
                                        <div style="width:100%;text-align:center;">
                                            <h3>Seleccionados</h3>
                                        </div>
                                        <v-data-table
                                            :headers="headersseleccionados"
                                            :items="dataseleccionados"
                                            :items-per-page="5"
                                            :disable-sort="true"
                                        >
                                            <template v-slot:item.action="{ item }">
                                                <v-icon
                                                    small
                                                    class="mr-5"
                                                    color="primary"
                                                    @click="eliminarItem(item)"
                                                >
                                                    delete
                                                </v-icon>
                                            </template>
                                        </v-data-table>
                                    </v-col>
                                </v-row>
                            </v-form>
                        </v-layout>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="cerrarDialogo">Cancelar</v-btn>
                    <v-btn color="primary" text @click="saveitem" :disabled="!valid || dataseleccionados.length == 0">Guardar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialog_del" max-width="600px">
            <v-card>
                <v-card-title>
                    <span class="headline red--text">Eliminar Unspsc</span>
                </v-card-title>
                <v-card-text>
                    ¿Esta seguro de eliminar la Información seleccionada?
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" dark text @click="closeDialogoDel">Cancelar</v-btn>
                    <v-btn color="red" text @click="deleteItem" >Eliminar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-layout>
</template>

<script>
import { registroProponentes } from '@/services/api'

export default {
  name: 'regUnspsc',
  props: ['email', 'codigo'],
  data: () => ({
    dataUnspsc: [],
    search: '',
    dialog: false,
    dialog_del: false,
    itemactual: '',
    valid: false,
    unspsctxt: '',
    unspsctxtant: '',
    unspsc: 0,
    filtro: '',
    valfiltro: '',
    searchfiltro: '',
    dataBusq: [],
    dataseleccionados: [],
    headers: [
      { text: 'Código', align: 'left', sortable: false, value: 'codigounspsc', class: 'subtitle-1 font-weight-bold black--text' },
      { text: 'UNSPSC', align: 'left', sortable: false, value: 'descripcionunspsc', class: 'subtitle-1 font-weight-bold black--text' },
      { text: 'Acción', value: 'action', sortable: false, class: 'subtitle-1 font-weight-bold black--text' }
    ],
    headersbusqueda: [
      { text: 'Código', align: 'left', sortable: false, value: 'codigo', class: 'subtitle-1 font-weight-bold black--text' },
      { text: 'UNSPSC', align: 'left', sortable: false, value: 'nombre', class: 'subtitle-1 font-weight-bold black--text' },
      { text: 'Seleccionar', value: 'action', sortable: false, class: 'subtitle-1 font-weight-bold black--text' }
    ],
    headersseleccionados: [
      { text: 'Código', align: 'left', sortable: false, value: 'codigo', class: 'subtitle-1 font-weight-bold black--text' },
      { text: 'UNSPSC', align: 'left', sortable: false, value: 'nombre', class: 'subtitle-1 font-weight-bold black--text' },
      { text: 'Accion', align: 'left', sortable: false, value: 'action', class: 'subtitle-1 font-weight-bold black--text' }
    ],
    arrfiltro: [
      { text: 'Código', value: 'C' },
      { text: 'Clase', value: 'L' },
      { text: 'Familia', value: 'F' },
      { text: 'Segmento', value: 'S' },
      { text: 'UNSPSC', value: 'U' }
    ]
  }),
  methods: {
    resetForm () {
      this.unspsctxt = ''
      this.unspsctxtant = ''
      this.unspsc = 0
      this.filtro = ''
      this.valfiltro = ''
      this.searchfiltro = ''
      this.dataBusq = []
      this.dataseleccionados = []
    },
    cerrarDialogo () {
      this.resetForm()
      this.dialog = false
    },
    closeDialogoDel () {
      this.dialog_del = false
    },
    async getFormRegUnsPsc () {
      try {
        const { data } = await registroProponentes.getRegistroUnspsc({ codigoformregistro: this.codigo })
        this.dataUnspsc = data.data
      } catch (error) {
      } finally {
      }
    },
    async traerDataXFiltro () {
      this.dataBusq = []
      if (this.filtro === '' || this.valfiltro === '') {
        this.$toasted.error('Debe ingresar los datos de la búsqueda', { icon: 'error', duration: 5000 })
        return
      }
      var datafiltro = [{ tipo: this.filtro, valor: this.valfiltro }]
      try {
        const { data } = await registroProponentes.getUnspscXFiltro({ data: datafiltro })
        this.dataBusq = data.data
      } catch (error) {
      } finally {
      }
    },
    async saveitem () {
      if (this.dataseleccionados.length <= 0) {
        this.$toasted.error('No ha seleccionado ningún Código', { icon: 'error', duration: 5000 })
      } else {
        try {
          const { data } = await registroProponentes.saveRegistroUnspsc({ data: this.dataseleccionados })
          if (data) {
            this.$toasted.success('Se agregarón los códigos Unspsc con éxito', { icon: 'done', duration: 5000 })
            this.$store.commit('updateRegistro', true)
            this.dialog = false
            this.getFormRegUnsPsc()
          } else {
            this.$toasted.error('Se presentó un error al tratar de agregar los códigos', { icon: 'error', duration: 5000 })
          }
        } catch (error) {
        } finally {
        }
      }
    },
    async deleteItem () {
      try {
        const { data } = await registroProponentes.delRegistroUnspsc({ codigoformregistro: this.itemactual.codigoformregistro, codigounspsc: this.itemactual.codigounspsc })
        if (data) {
          this.$toasted.success('Se eliminó el código Unspsc con éxito', { icon: 'done', duration: 5000 })
          this.dialog_del = false
          this.getFormRegUnsPsc()
        } else {
          this.$toasted.error('Se presentó un error al intentar eliminar el Código Unspsc', { icon: 'error', duration: 5000 })
        }
      } catch (error) {
      } finally {
      }
    },
    seleccionarItem (itemsel) {
      // primero los seleccionados
      for (let index = 0; index < this.dataseleccionados.length; index++) {
        const element = this.dataseleccionados[index]
        if (itemsel.codigo === element.codigo) {
          this.$toasted.error('El Código Unspsc ya fué seleccionado', { icon: 'error', duration: 5000 })
          return
        }
      }
      // los que ya existen
      for (let index = 0; index < this.dataUnspsc.length; index++) {
        const element = this.dataUnspsc[index]
        if (itemsel.codigo === element.codigounspsc) {
          this.$toasted.error('El Código Unspsc ya existe', { icon: 'error', duration: 5000 })
          return
        }
      }
      this.dataseleccionados.push({ codigoregistro: this.codigo, codigo: itemsel.codigo, nombre: itemsel.nombre })
    },
    eliminarItem (itemsel) {
      // primero los seleccionados
      for (let index = 0; index < this.dataseleccionados.length; index++) {
        const element = this.dataseleccionados[index]
        if (itemsel.codigo === element.codigo) {
          this.dataseleccionados.splice(index, 1)
          return
        }
      }
    }
  },
  mounted () {
    this.getFormRegUnsPsc()
  }
}
</script>
